<template>
	<main class="zxhwheader">
		<van-nav-bar
		  title="专享好物"
		  rightText="按钮"
		  @click-left="onClickLeft"
		>
		<img src="../assets/zxhw/search.png" class="search" slot="left" />
		<img src="../assets/zxhw/buy_icon_shopping_cart@2x.png" class="buy" slot="right" />
		<img @click="showlist()" src="../assets/zxhw/sandian@2x.png" class="sand" slot="right" />
		</van-nav-bar>
		<van-popup v-model="showlistv" position="top" :overlay="false">
			<div class="sanjiao"></div>
			<ul class="content">
				<li>品鉴师</li>
				<li>收藏夹</li>
				<li>常见问题</li>
			</ul>
		</van-popup>
		<div class="nav">
			<ul class="row">
			  <li :class="{active:nowtab=='zxhw'}" @click="active('zxhw')">上新</li>
			  <li :class="{active:nowtab=='rexiao'}" @click="active('rexiao')">热销</li>
			  <li :class="{active:nowtab=='dingzhi'}" @click="active('dingzhi')">定制</li>
			  <li :class="{active:nowtab=='meishi'}" @click="active('meishi')">美食</li>
			  <li @click="showhide()"><img src="../assets/zxhw/index_arrow@2x.png" alt=""></li>
			</ul>
			<van-popup v-model="show" position="top" :overlay="true" closeOnClickOverlay="true" lockOnScroll="true" overlayClass="hideclass">
				<!-- 弹出层内容 -->
				<img @click="hidepanel()" src="../assets/zxhw/index_arrow_down@2x.png" alt="">
				<ul>
					<li :class="{active:nowtab==item.path}" v-for="item in showarr" @click="active(item.path)">{{item.name}}</li>
				</ul>
			</van-popup>
		</div>
	</main>
</template>
<script>
import "lib-flexible"
export default{
	name:'zxhwheader',
	data(){
		return{
			show:false,
			showlistv:false,
			// 弹出层内容数组
			showarr:[{'name':'上新','path':'zxhw'},{'name':'热销','path':'rexiao'},{'name':'定制','path':'dingzhi'},{'name':'美食','path':'meishi'},{'name':'生活','path':'shenghuo'},{'name':'旅行','path':'lvxing'},{'name':'数码','path':'shuma'},{'name':'文创','path':'wenchuang'},{'name':'玩物','path':'wanwu'},{'name':'酒饮','path':'jiuyin'},{'name':'箱包','path':'xiangbao'},{'name':'配饰','path':'peishi'},{'name':'运动','path':'yundong'},{'name':'个护','path':'gehu'},{'name':'礼物','path':'liwu'},{'name':'服饰','path':'fushi'}],
		}
	},
	props:{
		nowtab:{
			type:String,
			required:false,
			default:()=>{
				return 'zxhw'
			}
		}
	},
	methods:{
		change(val){
			this.$emit('routechange',val);
			this.show = false;
			this.showlistv = false;
		},
		active(k){
			this.change(k);
		},
		hidepanel(){
			this.show = false;
		},
		showhide(){
			this.show = true;
		},
		showlist(){
			if(this.showlistv){
				this.showlistv = false;
			}else{
				this.showlistv = true;
			}
		},
	    onClickLeft() {
	    	//搜索逻辑
	    },
	}
}
</script>
<style>
main.zxhwheader{
	position: fixed;
	top:0;
	width:100%;
	background-color: #fff;
	z-index: 2000;
}
/*导航*/
.van-nav-bar{
	height:88px;
	line-height: 88px;
	background-image: linear-gradient(90deg, #643872 0%, #825e68 100%), linear-gradient(#ffdb2b, #ffdb2b);
	color:#fff;
	font-size: 40px;
}
.van-nav-bar::after{
	border:none;
}
.van-nav-bar>.van-nav-bar__title{
	font-family: 'PingFang-SC-Medium';
	font-size: 40px;
}
.van-nav-bar>.van-nav-bar__left>img.search{
	width: 40px;
	height: 34px;
	position: relative;
	top:-0.05rem;
}
.van-nav-bar>.van-nav-bar__right>img.sand{
	width: 42px;
	height:10px;
	position: relative;
	top:-0.05rem;
}
.van-nav-bar>.van-nav-bar__right>img.buy{
	width: 50px;
	height:40px;
	position: relative;
	top:-0.05rem;
	margin-right: 38px;		
}
.row{
	margin-top: 2px;
	margin-bottom: 2px;
	display: flex;
	justify-content: space-around;
}
.row>li{
	width:102px;
	height:80px;
	line-height: 80px;
	text-align: center;
	font-size: 28px;
}
.row>li.active{
	background-color:#653a72;
	color:#fff;
}
.row>li>img{
	width:32px;
	height:18px;
}
/*弹出层*/
.van-nav-bar+.van-popup{
	top:90px;
	width: 208px;
	/*height: 3.666667rem;*/
	left:auto;
	right:-88px;
	background-color:transparent;
	transform: translate3d(-50%,0,0);
	-webkit-transform: translate3d(-50%,0,0);
}
.van-nav-bar+.van-popup>div.sanjiao{
	width: 0;
    height: 0;
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    border-bottom: 14px solid #f2f1f2;
    position: relative;
    top:1px;
    left:160px;		
}
.van-nav-bar+.van-popup>ul.content{
	padding:0 20px;
	background-color: rgba(242,242,242,0.9);
	border-radius: 20px;
}
.van-nav-bar+.van-popup>ul.content>li{
	padding:30px 0;
	color:#000;
	font-size: 28px;
	text-align: center;
	border-bottom: 1px solid #c1c1c1;
}
.van-nav-bar+.van-popup>ul.content>li:last-child{
	border-bottom: none;	
}
.nav>.van-popup{
	top:88px;
	text-align:right;
	transition: none;
	transform: translate3d(-50%,0px,0);
	-webkit-transform: translate3d(-50%,0px,0);
}
.nav>.van-popup>img{
	width:32px;
	display: block;
	height:18px;
	position: relative;
	left:91%;
	margin-top:40px;
}
.nav>.van-popup>ul{
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	text-align: center;
}
.nav>.van-popup>ul>li{
	width:33%;
	margin:50px 0;
	color:#000;
	font-size: 32px;
}
.nav>.van-popup>ul>li.active{
	color:#f42d06;
}
.hideclass{
	top:auto;
	background: rgba(0,0,0,0.4);;
}
.van-modal{
	top:88px;
}
div.val-modal{
	/*top:1.066667rem;*/
}
div.nav{
	position: relative;
}
</style>


