<template>
	<main class="zxhw">
		<zxhwheader @routechange="change"></zxhwheader>
		<section>
			<van-swipe :autoplay="5000">
			  <van-swipe-item v-for="(image, index) in images" :key="index">
			    <img :src="image" />
			  </van-swipe-item>
			</van-swipe>
			<ul class="info">
				<li><img src="../../assets/zxhw/shop_real@2x.png" alt="">品鉴师全球真选</li>
				<li><img src="../../assets/zxhw/shop_refund@2x.png" alt="">30天无忧退换货</li>
				<li><img src="../../assets/zxhw/shop_free@2x.png" alt="">特惠期邮费全免</li>
			</ul>
			<div class="content">
				<ul
					v-waterfall-lower="loadMore"
				    waterfall-disabled="disabled"
				    waterfall-offset="400">
				    <li v-for="item in list">
				    	<h1>{{item.day}}<span v-if="item.mounth">{{item.mounth}}</span></h1>
						<div class="item" v-for="prod in item.items">
							<img :src="prod.image" alt="">
							<p><img class="avater" :src="prod.avater" alt=""><span>{{prod.uname}}</span></p>
							<h3>{{prod.name}}</h3>
							<h2>{{prod.desp}}</h2>
							<ul>
								<li><span class="pay">￥{{prod.pay}}</span><span class="paybefore">￥{{prod.paybefore}}</span></li>
								<li>详情</li>
							</ul>
						</div>
					</li>
				</ul>
			</div>
		</section>
		<zxhwfooter :active='1'></zxhwfooter>
	</main>
</template>
<script>
import "lib-flexible"
import { Waterfall,Dialog } from 'vant';
import zxhwheader from '../../components/zxhwheader.vue'
import zxhwfooter from '../../components/zxhwfooter.vue'
export default{
	name:'zxhw',
	data(){
		return{
			images:[require('../../assets/zxhw/tz2.png'),require('../../assets/zxhw/tz2.png'),require('../../assets/zxhw/tz2.png')],
			//瀑布流（数据仅供参考 以接口为准）
			list: [{'day':'今日','mounth':'','items':[{'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'},{'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'}]},{'day':'28','mounth':'09月','items':[{'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'}]}],
		    disabled: false
		}
	},
	directives: {
	    WaterfallLower: Waterfall('lower'),
	},
	methods:{
		change(val){
			this.$router.push({'name':val});
		},
		loadMore() {
			//瀑布流加载更多
	        // this.disabled = true;
	        // setTimeout(() => {
	        // for (let i = 0; i < 5; i ++) {
	        //   this.list.push(this.list.length);
	        // }
	        // this.disabled = false;
	        // }, 200);
	    }
	},
	mounted(){

	},
	components:{
		zxhwheader,
		zxhwfooter
	}
}
</script>
<style lang='less'>
main.zxhw{
	width: 100%;
	background-color: #fff;
	padding-top: 160px;
	>section{
		width: 100%;
		padding-bottom: 100px;
		background-color: #fff;
		overflow: hidden;
		.van-swipe{
			width:100%;
			height:400px;	
		}
		.van-swipe-item{
			>img{
				// max-width:100%;
				width: 100%;
				height:400px;
				margin:0 auto;
			}
		}
		.van-swipe__indicators{
			bottom:0.186667rem;
			>i{
				background-color: rgba(255,255,255,0.2);
				&.van-swipe__indicator--active{
					background-color: #fff;
				}
			}
		}
		>ul.info{
			display: flex;
			height:120px;
			align-items:center;
			justify-content:center;
			border-bottom: solid 1px #dddddd;
			>li{
				flex:1;
				font-size: 20px;
				color: #d9bf7a;
				text-align: center;
				position: relative;
				top:0.05rem;
				>img{
					width:40px;
					height:40px;
					margin-right: 5px;
					position: relative;
					top:-0.05rem;
				}
			}
		}
		>div.content{
			padding:0 30px;
			>ul{
				>li{
					>h1{
						margin-top:40px;
						color:#000;
						font-size: 50px;
						>span{
							font-size: 0.32rem;
						}
					}
					>div.item{
						margin-top:40px;
						padding-bottom: 34px;
						border-bottom:1px solid #ddd;
						>img{
							width:100%;
							height:440px;
						}
						>p{
							margin:20px 0;
							>img{
								width:60px;
								height:60px;
								margin-right: 20px;
							}
							>span{
								color:#919191;
								font-size: 30px;
								position: relative;
								top:-0.1rem;
							}
						}
						>h2{
							font-size: 30px;
							color:#919191;
						}
						>h3{
							font-size: 36px;
							margin-bottom: 25px;
						}
						>ul{
							display: flex;
							align-items:center;
							margin-top: 10px; 
						    justify-content: space-between;
						    >li{
						    	&:first-child{
						    		position: relative;
						    		font-size: 30px;
						    		>span{
						    			&.pay{
											font-size: 32px;
											color:#f5401e;
						    			}
						    			&.paybefore{
											font-size: 30px;
											color:#777;
						    			}
						    		}
						    	}
						    	&:last-child{
						    		font-size: 24px;
						    		color:#919191;
						    		text-align: center;
						    		width: 128px;
						    		line-height: 47px;
									height: 47px;
									border-radius: 5px;
									border: solid 1px #bbbbbb;
						    	}
						    }
						}
					}
				}
			}
		}
	}
}
</style>



